import React, { Component } from 'react';
import { Table, Button } from 'semantic-ui-react';
import web3 from '../contracts/web3';
import Campaign from '../contracts/campaign';

class RequestRow extends Component {

    constructor(props) {
        super(props);
        this.state = {
            loading: false
        };
    }

    onApprove = async () => {
        this.setState({loading : true});

        const campaign = Campaign(this.props.address);
        const accounts = await web3.eth.getAccounts();
        await campaign.methods.approveRequest(this.props.id).send({
            from : accounts[0]
        });
        this.setState({loading : false});
    };

    onFinalize = async () => {
        this.setState({loading : true});

        const campaign = Campaign(this.props.address);
        const accounts = await web3.eth.getAccounts();
        await campaign.methods.finalizeRequest(this.props.id).send({
            from : accounts[0]
        })
    };

    render() {

        const { Row, Cell } = Table;
        console.log(this.props);
        return (
            <Row>
                <Cell>{this.props.id + 1}</Cell>
                <Cell>{this.props.request.description}</Cell>
                <Cell>{web3.utils.fromWei(this.props.request.value,'ether')} ETH</Cell>
                <Cell>{this.props.request.recipient}</Cell>
                <Cell>{this.props.request.approvalCount} / {this.props.approversCount}</Cell>
                <Cell>
                    {this.props.request.complete ? null : (
                        <Button color="green" basic onClick={this.onApprove}>승인</Button>
                    )}
                </Cell>
                <Cell>
                    <Button color="red" basic onClick={this.onFinalize}>출금하기</Button>
                </Cell>
            </Row>
        );
    }
}

export default RequestRow;